.footer{
    width: 100%;
    padding: 2rem 0;
    background-color: rgba(19,19,19,0.8);
}
.footer-container{
    max-width: 1140px;
    margin: auto;
    display: flex;
    grid-template-columns: repeat(2 1fr);
    grid-gap:40px
}
.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.social{
    margin-top: 1rem;
}
.fb{
    cursor: pointer;
}

@media screen and (max-width: 640px) {
    .footer-container{
        grid-template-columns: 1fr;
    }
}